@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  text-align: justify;
  margin: 0px 15px;
}

.center {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.page-contents {
  display: flex;
  padding: 20px;
}

.profile-img {
  float: left;
  height: 250px;
  padding: 20px;
  display: flex;
}
:root {  
  --violet-blue: #3a4fa0ff;
  --khaki-web: #c0ab97ff;
  --ash-gray: #bcbeb1ff;
  --independence: #3b3e53ff;
  --verdigris: #5ab0aeff;
}

.App {
  text-align: center;
}
